import socket from "../CLASSES/Socket";
import codes from "../CLASSES/opcode";

class RegisterResponse {
    /** @param {[Boolean,Boolean,Boolean,Boolean,Boolean,Boolean,Boolean,Boolean,String,String,String,String]} response */
    constructor(response = []) {
        this.done = response[0];
        this.error = response[1];
        this.unkownError = response[2];
        this.notReady = response[3];
        this.isEnded = response[4];
        this.isFull = response[5];
        this.username_used = response[6];
        this.email_used = response[7];
        this.e_username = response[8];
        this.e_password = response[9];
        this.e_email = response[10];
        this.e_race = response[11];
    }
}
/** @returns {Promise<RegisterResponse>} */
function requestRegister(data = {}) { return socket.request(codes.REGISTER, data); }
socket.defineStructure(codes.REGISTER, RegisterResponse);

export default requestRegister;