import socket from "../CLASSES/Socket";
import codes from "../CLASSES/opcode";

class ServersInfoList {
    /** @type {{ serverNumber: Number, ready: Boolean, ended: Boolean, isFull: Boolean, started: Number, players: Number}[]} */
    servers = [];
    /** @param {[Number,Number,Number,Number,Number,Number][]} servers */
    constructor(servers = []) {
        if (!Array.isArray(servers)) return;
        servers.forEach(server =>
            this.servers.push({
                serverNumber: server[0],
                ready: server[1] === 1,
                ended: server[2] === 1,
                isFull: server[3] === 1,
                started: server[4],
                players: server[5]
            })
        );
    }
}
/** @returns {Promise<ServersInfoList>} */
export default function requestServers() { return socket.request(codes.LIST_SERVERS); }
socket.defineStructure(codes.LIST_SERVERS, ServersInfoList);